.extra__heading {
  display: flex;
  align-items: center;
  padding-left: 20px;
  margin: 0;
  margin-bottom: 32px;

  @include fs-lh(24px, 24px);

  &::after {
    content: "";

    width: 10px;
    height: 3px;
    margin-left: 24px;
    margin-top: 8px;

    background-color: $green;
    flex-grow: 1;
  }
}

.extra__list {
  margin: 0 auto;
}

.extra__gift {
  position: relative;

  margin: 39px auto 35px;
  padding: 190px 55px 46px;
  width: 280px;
  height: 289px;

  box-sizing: border-box;
  text-align: center;
  @include Arial (16px, 20px);
  color: #ffffff;

  background-color: $green;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url("../img/gift-photo-mobile.jpg");

  @media screen and (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
  }

  &::before {
    content: "";

    position: absolute;
    top: 65px;
    left: 113px;

    width: 55px;
    height: 60px;

    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' fill='%23ffffff' viewBox='0 0 56 60'><path d='M52.3 12.2h-7c.5-.7.8-1.4 1.1-2.2.6-2 .4-4.1-.6-5.9-1-1.8-2.6-3.1-4.6-3.7-2-.6-4.1-.4-5.9.6-3.6 2-6.2 6.1-7.6 9-1.4-2.9-4-7.1-7.6-9-3.8-2-8.5-.6-10.5 3.1-1.4 2.7-1.2 5.8.5 8.1h-7c-1.7 0-3.1 1.4-3.1 3.1v9.4c0 1.7 1.4 3.1 3.1 3.1H4v26.1c0 3.3 2.7 6 6 6h35.5c3.3 0 6-2.7 6-6V27.8h.8c1.7 0 3.1-1.4 3.1-3.1v-9.4c.1-1.7-1.3-3.1-3.1-3.1zM36.5 3c1.3-.7 2.7-.8 4.1-.4 1.3.4 2.4 1.3 3.1 2.6.7 1.3.8 2.7.4 4.1-.4 1.2-1.1 2.2-2.2 2.9H29.4C30.5 9.6 33 5 36.5 3zM11.7 5.2C13.2 2.6 16.4 1.6 19 3c3.5 1.9 6 6.6 7.1 9.1H13.5c-2.3-1.4-3.1-4.5-1.8-6.9zm14.8 22.6v13.7H6.4V27.8h20.1zM6.4 53.9v-10h20.2v13.7H10c-2 0-3.6-1.7-3.6-3.7zm39.1 3.7H28.9V43.9h20.2v10c0 2-1.6 3.7-3.6 3.7zm3.6-16.1H28.9V27.8h20.2v13.7zm4-16.8c0 .4-.3.7-.7.7H3.2c-.4 0-.7-.3-.7-.7v-9.4c0-.4.3-.7.7-.7h49.2c.4 0 .7.3.7.7v9.4z'/></svg>");
    background-repeat: no-repeat;
    fill: #ffffff;
  }
}

@media screen and (min-width: $tablet-width) {
  .extra__heading {
    padding: 0;
    margin-bottom: 75px;

    @include fs-lh(36px, 36px);

    &::before {
      content: "";

      width: 28px;
      height: 3px;
      margin-right: 75px - 28px;
      margin-top: 10px;

      background-color: $green;
    }

    &::after {
      content: "";

      margin-left: 35px;
      margin-top: 10px;
    }
  }

  .extra__gift {
    margin: 69px auto 79px;
    padding: 75px 120px 70px 423px;

    width: 708px;
    height: 200px;

    text-align: left;
    background-image: url("../img/gift-photo-tablet.jpg");

    @media screen and (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-tablet@2x.jpg");
    }

    span {
      display: block;
    }

    &::before {
      left: 91px;
    }
  }
}

@media screen and (min-width: $desktop-width) {
  .extra__wrapper {
    padding: 0 110px;
    display: flex;
    justify-content: flex-start;
  }

  .extra__list {
    margin: 0;
    margin-right: 80px;
  }

  .extra__gift {
    padding: 195px (245px - 165px)/2 47px;
    margin: 0;
    width: 245px;
    height: 289px;

    text-align: center;
    background-image: url("../img/gift-photo-desktop.jpg");

    @media screen and (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/gift-photo-desktop@2x.jpg");
    }

    &::before {
      left: 96px;
    }
  }
}
